import React from 'react';

function Button({ set, id, bought}){
   
    return(
    <div className="card-button">
        <button className="btn"  onClick={()=>set(id)} disabled={bought} >
        {bought? 'Добавлено' : 'Добавить в корзину'}
        </button>
    </div>
    )
}

export default Button;