import React from 'react';
import Logo from './Logo/Logo';
import Menu from './Menu';
import Checkout from './Checkout/Checkout';

function Header({base}){

    

    return(
    <div className="header">
        <Logo/>
        <Menu/>
        <Checkout base={base}/>
        <a href="#">выход</a>
    </div>)
}

export default Header;